<template>
  <div>
    <div class="reference">
      <div class="reference-label">参考知识点：</div>
      <div class="reference-content">
        <div class="text" style="white-space: pre-line;">
          {{ taskScienceSchedule ? taskScienceSchedule.desc : '' }}
        </div>
        <div class="image-list">
          <el-row>
            <el-col
              v-for="(image, index) in taskScienceSchedule
                ? taskScienceSchedule.icons
                : []"
              :key="index"
              :span="2"
            >
              <img
                :src="image.url"
                width="50"
                height="50"
                @click="showImage(image, taskScienceSchedule.icons)"
              />
            </el-col>
          </el-row>
        </div>
      </div>
    </div>
    <div class="tips">
      <div class="tips-title">
        温馨提示：
      </div>
      <div class="tips-content">
        以上知识点为教研团队提供的参考内容，用户不可见。{{ tips }}
      </div>
    </div>
    <!--图片显示器-->
    <monitor
      ref="monitor"
      :monitor-info="monitorInfo"
      :current-image-url-arr="curUrlList"
    />
  </div>
</template>

<script>
import Monitor from '@/components/Monitor'

export default {
  name: 'GuidanceReference',
  components: {
    Monitor
  },
  props: {
    taskScienceSchedule: {
      type: Object,
      default: () => {}
    },
    tips: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      curUrlList: [],
      monitorInfo: {}
    }
  },
  methods: {
    // 查看图片详情
    showImage(image, urlList) {
      this.curUrlList = urlList.map(({ url }) => url)
      this.monitorInfo = {
        type: image.type,
        meta: { url: image.url }
      }
      this.$refs.monitor.show()
    }
  }
}
</script>

<style scoped lang="scss">
@import '@/styles/healthManagement/reference.scss';
</style>
